import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {

    vg1_adm_oko_lpm: require('./assets/vg1_adm_og_oko_lpm.json'),
    vg1_mrkf_salg_lpm: require('./assets/vg1_mrkf_og_salg_lpm.json'),
    vg1_sikk_trans_lpm: require('./assets/vg1_sikkerhet_og_transp_lpm.json'),

    vg2_mrkf_salg_lpm: require('./assets/vg2_mrkf_og_salg_lpm.json'),
    vg2_oko_adm_lpm: require('./assets/vg2_oko_og_adm_lpm.json'),
    vg2_sikkerhet_lpm: require('./assets/vg2_sikkerhet_lpm.json'),

    vg3_kontor_adm_lpm: require('./assets/vg3_kontor_og_adm_lpm.json'),
    vg3_salgsfaget_lpm: require('./assets/vg3_salgsfaget_lpm.json'),
    vg3_sikkerhetsfaget_lpm: require('./assets/vg3_sikkerhetsfaget_lpm.json'),

    valgtElev:'',
    valgtKlasse:'',
    valgteLPM: [],
    klasse: []
  },
  getters: {
    get_vg1_adm_oko_lpms: state => {
      return state.vg1_adm_oko_lpm
    },
    get_vg1_mrkf_salg_lpms: state => {
      return state.vg1_mrkf_salg_lpm
    },
    get_vg1_sikk_trans_lpms: state => {
      return state.vg1_sikk_trans_lpm
    },

    get_vg2_mrkf_salg_lpms: state => {
      return state.vg2_mrkf_salg_lpm
    },
    get_vg2_oko_adm_lpms: state => {
      return state.vg2_oko_adm_lpm
    },
    get_vg2_sikkerhet_lpms: state => {
      return state.vg2_sikkerhet_lpm
    },

    get_vg3_kontor_adm_lpms: state => {
      return state.vg3_kontor_adm_lpm
    },
    get_vg3_salgsfaget_lpms: state => {
      return state.vg3_salgsfaget_lpm
    },
    get_vg3_sikkerhetsfaget_lpms: state => {
      return state.vg3_sikkerhetsfaget_lpm
    },

    getValgteLPM: state => {
      return state.valgteLPM
    },

    get_klasse: state => {
      return state.klasse
    },
    
    get_valgtElev: state => {
      return state.valgtElev
    },

    get_valgtKlasse: state => {
      return state.valgtKlasse
    }
  },
  mutations: {
    addElev: (state, payload) => {
      state.klasse.push(payload)
    },
    addLpm: (state, payload) => {
      state.valgteLPM.push(payload)
    },
    removeLpm: (state, payload) => {
      state.valgteLPM = state.valgteLPM.filter(item => item.id !== payload.id)
    },
    clearValgte: state => {
      // KAN HENDE VI MÅ SETTE CHECKED = FALSE PÅ ALLE LPMS SOM ER BLITT VALGT
      // FØR VI KAN SETTE DEN TOM
      state.valgteLPM = []
    },
    velgElev: (state, payload) => {
      state.valgtElev = payload
    },
    velgKlasse: (state, payload) => {
      state.valgtKlasse = payload
    },
    /* HER MÅ FORNAVN OG ETTERNAVN BYTTES NÅR VI FÅR RETT CSV */
    setFornavn: (state, payload) => {
      state.valgtElev.Etternavn = payload
    },
    setEtternavn: (state, payload) => {
      state.valgtElev.Fornavn = payload
    },
    setKlasse: (state, payload) => {
      state.valgtElev.Klasse = payload
    },
    setYear: (state, payload) => {
      state.valgtElev.Skoleår = payload
    },
    setTeacher: (state, payload) => {
      state.valgtElev.Faglærer = payload
    },
    setUtdanningsprogram: (state, payload) => {
      state.valgtElev.Utdanningsprogram = payload
    },
    setSkole: (state, payload) => {
      state.valgtElev.Skole = payload
    },
    //set prosjekt details
    setProsjektYearlyHours: (state, payload) => {
      state.valgtElev.Prosjekter[0].yearlyHours = payload
    },
    setProsjektOrganisering: (state, payload) => {
      state.valgtElev.Prosjekter[0].organisering = payload
    },
    setProsjektLaerefag: (state, payload) => {
      state.valgtElev.Prosjekter[0].laerefag = payload
    },
    setProjektNr: (state, payload) => {
      state.valgtElev.Prosjekter[0].prosjektNr = payload
    },
    setDato: (state, payload) => {
      state.valgtElev.Utskriftsdato = payload
    },
    leggTilProps: (state, payload) => {
      Vue.set(state.valgtElev, 'Utdanningsprogram', payload.utdanningsprogram)
      Vue.set(state.valgtElev, 'Skole', payload.skole)
      Vue.set(state.valgtElev, 'Utskriftsdato', payload.dato)
      Vue.set(state.valgtElev, 'Prosjekter', payload.prosjekter)
    }
  },
  actions: {

  }
})
