import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path:'/',
      name:'home',
      // route level code-splitting
      // this generates a separate chunk (home.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "home" */ './views/Home.vue')
    },
    {
      path: '/YFF',
      name: 'YFF',
      component: () => import(/* webpackChunkName: "YFF" */ './views/YFF.vue')
    },
    {
      path:'/YFF_Godkjenn',
      name:'YffGodkjenn',
      // route level code-splitting
      // this generates a separate chunk (home.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "YffGodkjenn" */ './views/YffGodkjenn.vue')
    },
    {
      path:'/YFF_print',
      name:'YffPrint',
      // route level code-splitting
      // this generates a separate chunk (home.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "YffPrint" */ './views/YFFpdf.vue')
    },
    {
      path: '/Eksamen',
      name: 'Eksamen',
      component: () => import(/* webpackChunkName: "Eksamen" */ './views/Eksamen.vue')
    },
    {
      path: '*', redirect:'/'
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    if(savedPosition){
      return savedPosition
    } else {
      return {x: 0, y: 0 }
    }
  }
})
